"use client";

import { useState } from "react";
import { DashboardWrapper } from "@/components/layout/dashboard-wrapper";
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { ScheduleSessionModal } from "@/components/scheduling/schedule-session-modal";
import { CalendarPlus, User, TrendingUp, Star, BookOpen } from "lucide-react";
import Link from "next/link";
import { format } from 'date-fns';

// Mock Student Data Interface
interface Student {
  id: string;
  name: string;
  avatar: string;
  email: string;
  subjects: string[];
  progress?: number; // Optional: Example stat
  lastSession?: Date; // Optional: Example stat
}

// Mock Data (replace with actual data fetching)
const mockStudents: Student[] = [
  {
    id: "student1",
    name: "John Doe",
    avatar: "/avatars/john-doe.png",
    email: "john.doe@example.com",
    subjects: ["Calculus", "Physics"],
    progress: 85,
    lastSession: new Date(Date.now() - 3 * 24 * 60 * 60 * 1000), // 3 days ago
  },
  {
    id: "student2",
    name: "Jane Smith",
    avatar: "/avatars/jane-smith.png",
    email: "jane.smith@example.com",
    subjects: ["Physics", "Chemistry"],
    progress: 78,
    lastSession: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000), // 5 days ago
  },
  {
    id: "student3",
    name: "Alice Brown",
    avatar: "/avatars/alice-brown.png",
    email: "alice.brown@example.com",
    subjects: ["Chemistry"],
    progress: 92,
    lastSession: new Date(Date.now() - 1 * 24 * 60 * 60 * 1000), // 1 day ago
  },
];

export default function TutorStudentsPage() {
  const [isScheduleModalOpen, setIsScheduleModalOpen] = useState(false);
  const [selectedStudent, setSelectedStudent] = useState<Student | null>(null);

  const handleScheduleClick = (student: Student) => {
    setSelectedStudent(student);
    setIsScheduleModalOpen(true);
  };

  return (
    <DashboardWrapper>
      <div className="container mx-auto py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold">My Students</h1>
          <p className="text-muted-foreground">Manage your students and schedule sessions.</p>
        </div>

        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {mockStudents.map((student) => (
            <Card
              key={student.id}
              className="flex flex-col transition-all duration-200 ease-in-out hover:shadow-lg hover:-translate-y-1"
            >
              <CardHeader className="flex flex-row items-center gap-4 space-y-0 pb-4">
                 <Avatar className="h-12 w-12 border">
                    <AvatarImage src={student.avatar} alt={student.name} />
                    <AvatarFallback>{student.name.split(' ').map(n => n[0]).join('')}</AvatarFallback>
                 </Avatar>
                 <div className="flex-1">
                    <CardTitle>{student.name}</CardTitle>
                    <CardDescription className="text-xs truncate">{student.email}</CardDescription>
                    {/* Example stats - enhance as needed */}
                    <div className="flex items-center gap-2 mt-1 text-xs text-muted-foreground">
                       {student.progress && <span className="inline-flex items-center"><TrendingUp className="h-3 w-3 mr-1 text-primary/80"/> {student.progress}%</span>}
                       {/* Add more stats like avg rating if available */}
                    </div>
                 </div>
              </CardHeader>
              <CardContent className="flex-1 pt-0">
                 <div className="space-y-2 mb-4">
                    <h4 className="text-sm font-medium inline-flex items-center gap-1.5">
                       <BookOpen className="h-4 w-4 text-primary/80"/> Subjects
                    </h4>
                     <div className="flex flex-wrap gap-1">
                        {student.subjects.map((subject) => (
                           <Badge key={subject} variant="secondary">{subject}</Badge>
                        ))}
                     </div>
                 </div>
                 {/* Optional: Add last session date or other info */}
                 {student.lastSession && (
                    <p className="text-xs text-muted-foreground">
                        Last Session: {format(student.lastSession, 'PP')}
                    </p>
                 )}
              </CardContent>
              <CardFooter className="gap-2 pt-4">
                 <Button variant="outline" size="sm" className="flex-1" asChild>
                    <Link href={`/dashboard/tutor/students/${student.id}`}>
                        <User className="mr-2 h-4 w-4" /> View Profile
                    </Link>
                 </Button>
                 <Button size="sm" className="flex-1" onClick={() => handleScheduleClick(student)}>
                    <CalendarPlus className="mr-2 h-4 w-4" /> Schedule
                 </Button>
              </CardFooter>
            </Card>
          ))}
        </div>
      </div>

      {/* Scheduling Modal */}
      <ScheduleSessionModal
        isOpen={isScheduleModalOpen}
        onClose={() => setIsScheduleModalOpen(false)}
        role="TUTOR"
        availableUsers={selectedStudent ? [selectedStudent] : []} // Pass only the selected student
      />
    </DashboardWrapper>
  );
} 